import React from 'react'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Checkbox from '@material-ui/core/Checkbox';

export default function Table({ tasks, changeStatus, removeTask }) {
    tasks = tasks.map((value, index) => {
        const cn = value.status ? "task-text checked" : "task-text";
        return (
            <ListItem key={index} role={undefined} dense button onClick={() => changeStatus(index)}>
                <ListItemIcon>
                    <Checkbox
                        checked={value.status}
                        color="primary"
                        tabIndex={-1}
                        disableRipple
                        edge="start"
                        inputProps={{ 'aria-labelledby': index }}
                    />
                </ListItemIcon>
                <ListItemText className={cn} id={index} primary={value.text} />
                <ListItemSecondaryAction onClick={() => removeTask(index)}>
                    <IconButton edge="end" aria-label="comments">
                        <DeleteIcon />
                    </IconButton>
                </ListItemSecondaryAction>
            </ListItem>
        )
    })
    return (
        <List className="list">
            {tasks}
        </List>
    )
}
